html {
    font-size: 10px;
    background: -webkit-linear-gradient(323deg,  rgba(0, 0, 0, 0.9), rgba(255, 255, 255, 0.7) 70%),
                -webkit-linear-gradient(54deg, rgba(22, 165, 190, 0.623), rgba(0,0,0,0.8) 52%);
    background: -o-linear-gradient(323deg,  rgba(0, 0, 0, 0.9), rgba(255, 255, 255, 0.7) 70%),
                -o-linear-gradient(54deg, rgba(22, 165, 190, 0.623), rgba(0,0,0,0.8) 52%);
    background: linear-gradient(127deg,  rgba(0, 0, 0, 0.9), rgba(255, 255, 255, 0.7) 70%),
                linear-gradient(36deg, rgba(22, 165, 190, 0.623), rgba(0,0,0,0.8) 52%);
  }

  body,html {
    margin: 0;
    padding: 0;
    font-family: sans-serif;
  }
  
  .keys {
    display: -ms-flexbox;
    display: flex;
    -ms-flex: 1 1;
        flex: 1 1;
    min-height: 100vh;
    -ms-flex-align: center;
        align-items: center;
    -ms-flex-pack: center;
        justify-content: center;
  }
  
  .key {
    border: .4rem solid black;
    border-radius: .5rem;
    margin: 1rem;
    font-size: 1.5rem;
    padding: 1rem .5rem;
    -webkit-transition: all .07s ease;
    -o-transition: all .07s ease;
    transition: all .07s ease;
    width: 10rem;
    text-align: center;
    color: white;
    background: rgba(0,0,0,0.4);
    text-shadow: 0 0 .5rem black;
  }
  
  .playing {
    -webkit-transform: scale(1.1);
        -ms-transform: scale(1.1);
            transform: scale(1.1);
    border-color: #ffc600;
    -webkit-box-shadow: 0 0 1rem #ffc600;
            box-shadow: 0 0 1rem #ffc600;
  }
  
  kbd {
    display: block;
    font-size: 2rem;
  }
  
  .sound {
    font-size: 1.2rem;
    text-transform: uppercase;
    letter-spacing: .1rem;
    color: #ffc600;
  }
  


